*, *:before, *:after {
    box-sizing: border-box;
}

html, body {
    margin: 0;
    padding: 0;
}

*, a, input, select, textarea, button {
    font-family: 'PTRootUI', sans-serif;
}

.svg_icons {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 1px;
    height: 1px;
    overflow: hidden;
    opacity: 0;
    pointer-events: none;
}

.container {
    width: 968px;
    margin: 0 auto;
}

.container-fluid {
    width: 1316px;
    margin: 0 auto;
}

.club_card {
    display: block;
    position: relative;
    text-decoration: none;
    border-radius: 14px;
    box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.25);
    height: 100%;

    .search_club_img_wrapper {
        position: relative;

        .search_club_img {
            img {
                width: 100%;
                border-radius: 14px 14px 0 0;
            }
        }

        .club_services {
            position: absolute;
            left: 12px;
            bottom: 12px;
            display: flex;
            flex-direction: row;
            justify-content: flex-start;
            align-items: center;
            padding: 4px 8px;
            margin: 0 -4px;
            background: rgba(85, 85, 85, 0.5);
            backdrop-filter: blur(10.4615px);
            border-radius: 8px;

            img {
                display: block;
                width: 18px;
                height: 18px;
                margin: 0 4px;
            }
        }

        .club_distance {
            position: absolute;
            right: 12px;
            bottom: 12px;
            display: flex;
            flex-direction: row;
            justify-content: flex-start;
            align-items: center;
            padding: 4px 8px 4px 12px;
            background: rgba(85, 85, 85, 0.5);
            backdrop-filter: blur(10.4615px);
            border-radius: 8px;

            img {
                display: block;
                width: 18px;
                height: 18px;
                margin-right: 8px;
            }

            span {
                font-style: normal;
                font-weight: 500;
                font-size: 14px;
                line-height: 1.2;
                color: #ffffff;
            }
        }

        .club_promotion {
            position: absolute;
            top: 25px;
            left: 0;
            display: flex;
            align-items: center;
            justify-content: flex-end;
            width: 50px;
            height: 26px;
            background-color: #dc0000;

            span {
                font-style: normal;
                font-weight: 500;
                font-size: 14px;
                line-height: 1.2;
                color: #ffffff;
            }

            &:before {
                content: '';
                position: absolute;
                top: 0;
                left: 100%;
                display: block;
                height: 100%;
                width: 5px;
                border: 13px solid #dc0000;
                border-right: 6px solid transparent;
            }
        }
    }

    .search_club_info {
        display: flex;
        flex-direction: column;
        padding: 12px;

        .club_name {
            margin-bottom: 8px;

            span {
                position: relative;
                font-style: normal;
                font-weight: 700;
                font-size: 20px;
                line-height: 1.2;
                color: #000000;
            }

            &.approve {
                span:before {
                    content: '';
                    position: absolute;
                    top: 50%;
                    right: -30px;
                    display: block;
                    width: 19px;
                    height: 19px;
                    transform: translate(0, -50%);
                    background-image: url("../img/ok.svg");
                    background-repeat: no-repeat;
                    background-position: center;
                    background-size: 19px 19px;
                }
            }
        }

        .rating_wrapper {
            display: flex;
            flex-direction: row;
            justify-content: flex-start;
            align-items: center;
            margin-bottom: 16px;

            .rating_stars {
                display: flex;
                flex-direction: row;
                justify-content: flex-start;
                align-items: stretch;
                margin: 0 11px 0 -4px;

                img {
                    display: block;
                    width: 18px;
                    height: 18px;
                    margin: 0 4px;
                }
            }

            .reviews_qty {
                span {
                    font-style: normal;
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 1.2;
                    color: #000000;
                }
            }
        }

        .club_subway_wrapper {
            display: flex;
            flex-direction: row;
            justify-content: flex-start;
            align-items: center;
            margin-bottom: 12px;

            .subway_img_wrapper {
                margin-right: 8px;

                img {
                    display: block;
                    width: 20px;
                    height: 20px;
                }
            }

            .subway_station {
                font-style: normal;
                font-weight: 400;
                font-size: 16px;
                line-height: 1.3;
                color: #000000;
            }
        }

        .club_address_wrapper {
            display: flex;
            flex-direction: row;
            justify-content: flex-start;
            align-items: center;
            min-height: 52px;
            margin-bottom: 20px;

            .address_img_wrapper {
                margin-right: 10px;

                img {
                    display: block;
                    width: 20px;
                    height: 20px;
                }
            }

            .club_address {
                font-style: normal;
                font-weight: normal;
                font-size: 16px;
                line-height: 1.3;
                color: #000000;
            }
        }

        .club_features_wrapper {
            display: flex;
            flex-direction: row;
            justify-content: flex-start;
            align-items: stretch;
            margin: 0 -15px 60px;

            .club_features_item {
                margin: 0 15px;
                position: relative;

                .club_features_img_wrapper {
                    img {
                        display: block;
                        width: 30px;
                        height: 30px;
                    }
                }

                .club_features_qty {
                    display: block;
                    position: absolute;
                    top: 0;
                    right: 0;
                    background: #dc0000;
                    border-radius: 6px;
                    padding: 3px 4px;
                    font-style: normal;
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 1;
                    text-align: center;
                    color: #ffffff;
                    transform: translate(50%, -50%);
                }
            }
        }

        .club_price_wrapper {
            position: absolute;
            right: 0;
            bottom: 12px;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            height: 34px;
            width: 100%;
            padding: 0 12px;

            .club_price {
                font-style: normal;
                font-weight: 700;
                font-size: 20px;
                line-height: 1;
                color: #000000;
            }

            .club_close {
                font-style: normal;
                font-weight: 500;
                font-size: 18px;
                line-height: 1;
                color: #dc0000;
            }

            .club_booking, .club_calling {
                display: flex;
                flex-direction: row;
                justify-content: center;
                align-items: center;
                width: 151px;
                height: 34px;
                font-style: normal;
                font-weight: 500;
                font-size: 18px;
                line-height: 30px;
                text-align: center;
                color: #ffffff;
                border-radius: 12px;
            }

            .club_booking {
                background-color: #dc0000;
            }

            .club_calling {
                background-color: #000000;
            }
        }
    }

    &:hover {
        box-shadow: 0 0 15px #9c9c9c;
    }
}

.show_more {
    align-self: center;
    text-decoration: none;
    padding: 4px;
    margin: 0 auto;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 1;
    color: #000000;
    border-bottom: 1px solid transparent;
    transition: border-bottom-color 0.2s;

    &:hover {
        border-bottom: 1px solid #000000;
    }
}

.text_decor {
    color: #dc0000;
    white-space: nowrap;
}

/*! HEADER START */
.header {
    padding: 24px 0;

    .header_wrapper {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        .header_logo_wrapper {
            a {
                display: block;
                text-decoration: none;
                width: 198px;
                height: 46px;

                img {
                    display: block;
                    width: 100%;
                    height: 100%;
                }
            }
        }

        .select_city_wrapper {
            width: 136px;
            margin-left: 47px;

            .select2 {
                display: block;
                height: 34px;
                background: #dc0000;
                border-radius: 12px;

                .selection {
                    display: block;
                    width: 100%;
                    height: 100%;
                }

                .select2-selection {
                    background-color: #dc0000;
                    border: none;
                    outline: none;
                    border-radius: 12px;

                    .select2-selection__rendered {
                        width: 100%;
                        padding: 8px 8px 8px 42px;
                        font-style: normal;
                        font-weight: normal;
                        font-size: 18px;
                        line-height: 1;
                        color: #ffffff;

                        &:before {
                            content: '';
                            display: block;
                            width: 22px;
                            height: 22px;
                            position: absolute;
                            top: 50%;
                            left: 12px;
                            background-image: url(../img/bx-world.svg);
                            background-repeat: no-repeat;
                            background-position: center;
                            background-size: 22px 22px;
                            transform: translateY(-50%);
                        }
                    }

                    .select2-selection__arrow {
                        width: 12px;
                        height: 8px;
                        top: 50%;
                        right: 12px;
                        background-image: url("../img/role.svg");
                        background-repeat: no-repeat;
                        background-size: 12px 8px;
                        background-position: center;
                        transform: translateY(-50%);
                        transition: transform 0.2s;

                        b {
                            border-width: 0;
                        }
                    }
                }

                &.select2-container--open {
                    .select2-selection {
                        .select2-selection__arrow {
                            transform: rotate(180deg) translateY(50%);
                        }
                    }
                }
            }

            .select2-container {
                .select2-dropdown {
                    border: none;

                    .select2-results {
                        box-shadow: 0 0 27px rgba(0, 2, 4, 0.09);
                        background-color: #ffffff;
                        padding: 15px 10px;
                        margin-top: 0;
                        border-radius: 12px;

                        .select2-results__option {
                            font-size: 18px;
                            line-height: 22px;

                            &--highlighted[aria-selected] {
                                color: #dc0000;
                                background-color: #ffffff;
                            }
                        }
                    }
                }
            }
        }

        .header_menu_wrapper {
            ul {
                margin: 0;
                padding: 0;
                list-style: none;
                display: flex;
                flex-direction: row;
                justify-content: flex-start;
                align-items: center;
                margin: 0 -12px;

                li {
                    a {
                        display: block;
                        margin: 0 12px;
                        text-decoration: none;
                        font-style: normal;
                        font-weight: 500;
                        font-size: 18px;
                        line-height: 1;
                        color: #000000;
                        transition: color 0.2s;

                        &:hover {
                            color: #dc0000;
                        }
                    }
                }
            }
        }
    }
}
/*! HEADER END */

/*! SECTION CHOOSE CLUB INFO START */
section.choose_club_info_wrapper {
    .choose_club_info {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        padding: 32px 0 32px;

        .choose_club_info_title {
            h1 {
                font-family: 'Gilroy', sans-serif;
                font-style: normal;
                font-weight: 600;
                font-size: 34px;
                line-height: 1;
                letter-spacing: 0.25px;
                color: #000000;
            }
        }

        .choose_club_info_img {
            width: 424px;
            height: 282px;

            img {
                display: block;
                width: 100%;
                height: 100%;
            }
        }
    }
}
/*! SECTION CHOOSE CLUB INFO END */

/*! SECTION SEARCH CLUB START */
section.search_club_wrapper {
    .search_club {
        padding: 0 0 63px;
        border-bottom: 1px solid #d3d3d3;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;

        .search_club_sort_wrapper {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            width: 100%;
            padding: 0 36px 0 18px;
            margin-bottom: 32px;

            .search_club_sort {
                display: flex;
                flex-direction: row;
                justify-content: flex-start;
                align-items: center;

                .search_club_result {
                    margin-right: 12px;
                    font-style: normal;
                    font-weight: 700;
                    font-size: 18px;
                    line-height: 1;
                    color: #000000;

                    .search_qty {
                        font-weight: 400;
                    }
                }

                .sort_by {
                    display: flex;
                    flex-direction: row;
                    justify-content: flex-start;
                    align-items: center;

                    .sort_by_title {
                        font-style: normal;
                        font-weight: 700;
                        font-size: 18px;
                        line-height: 1;
                        color: #000000;
                        margin-right: 13px;
                    }

                    a {
                        display: block;
                        position: relative;
                        margin-right: 12px;
                        padding: 6px 0 6px 24px;
                        text-decoration: none;
                        font-style: normal;
                        font-weight: normal;
                        font-size: 18px;
                        line-height: 1;
                        color: #000000;
                        border-bottom: 4px solid transparent;
                        transition: opacity 0.2s, border-bottom-color 0.2s;

                        &:before {
                            content: '';
                            display: block;
                            width: 18px;
                            height: 18px;
                            position: absolute;
                            top: 6px;
                            left: 0;
                            background-image: url("../img/sort.svg");
                            background-repeat: no-repeat;
                            background-position: center;
                            background-size: 18px 18px;
                            opacity: 0;
                        }

                        &.active {
                            border-bottom: 4px solid #dc0000;

                            &:before {
                                opacity: 1;
                            }
                        }
                    }
                }


            }

            .search_club_show {
                display: flex;
                flex-direction: row;
                justify-content: flex-start;
                align-items: center;

                .show_by_list {
                    a {
                        display: block;
                        margin-right: 30px;
                        position: relative;
                        text-decoration: none;
                        padding: 8px 12px 8px 42px;
                        background: #dc0000;
                        border-radius: 12px;
                        font-style: normal;
                        font-weight: 500;
                        font-size: 18px;
                        line-height: 1;
                        color: #ffffff;

                        &:before {
                            content: '';
                            display: block;
                            width: 22px;
                            height: 22px;
                            position: absolute;
                            top: 50%;
                            left: 12px;
                            background-image: url("../img/list.svg");
                            background-repeat: no-repeat;
                            background-position: center;
                            background-size: 18px 18px;
                            transform: translateY(-50%);
                        }
                    }
                }

                .show_by_map {
                    a {
                        display: block;
                        position: relative;
                        text-decoration: none;
                        font-style: normal;
                        font-weight: 500;
                        font-size: 18px;
                        line-height: 1;
                        color: #000000;
                        padding: 8px 0 8px 30px;

                        &:before {
                            content: '';
                            position: absolute;
                            top: 50%;
                            left: 0;
                            display: block;
                            width: 22px;
                            height: 22px;
                            background-image: url("../img/point.svg");
                            background-repeat: no-repeat;
                            background-position: center;
                            background-size: 16px 20px;
                            transform: translateY(-50%);
                        }
                    }
                }
            }
        }


        .search_club_list {
            display: flex;
            flex-direction: row;
            justify-content: flex-start;
            align-items: stretch;
            flex-wrap: wrap;
            margin: 0 -22px;

            .search_club_item {
                width: calc(100% / 3 - 44px);
                margin: 0 22px 32px;
            }
        }
    }
}
/*! SECTION SEARCH CLUB END */

/*! SECTION ARTICLES START */
section.articles_wrapper {
    padding: 32px 0 122px;

    h2 {
        margin: 0;
        font-style: normal;
        font-weight: 500;
        font-size: 34px;
        line-height: 1.2;
        text-align: left;
        color: #000000;
    }

    .articles {
        display: flex;
        flex-direction: column;

        .articles_list {
            display: flex;
            flex-direction: row;
            justify-content: flex-start;
            align-items: stretch;
            flex-wrap: wrap;
            margin: 0 -35px;
            padding: 32px 0 0;

            .articles_item {
                width: calc(100% / 3 - 70px);
                margin: 0 35px 60px;

                a {
                    display: block;
                    text-decoration: none;
                    box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.25);
                    border-radius: 14px;
                    height: 100%;

                    .article_img_wrapper {
                        img {
                            display: block;
                            width: 100%;
                            border-radius: 14px 14px 0 0;
                        }
                    }

                    .article_content_wrapper {
                        padding: 12px 12px 18px;

                        .article_title {
                            font-style: normal;
                            font-weight: 700;
                            font-size: 20px;
                            line-height: 1.3;
                            color: #000000;
                            margin-bottom: 12px;
                        }

                        .article_descr {
                            font-style: normal;
                            font-weight: 400;
                            font-size: 18px;
                            line-height: 1.3;
                            color: #000000;
                            margin-bottom: 16px;
                        }

                        p {
                            margin: 0;
                            align-self: flex-end;
                            text-decoration: none;
                            font-style: normal;
                            font-weight: 500;
                            font-size: 18px;
                            line-height: 1;
                            text-align: right;
                            color: #dc0000;
                        }
                    }

                    &:hover {
                        box-shadow: 0 0 15px #9c9c9c;
                    }
                }
            }
        }

    }

}
/*! SECTION ARTICLES END */

/*! SECTION ARTICLE PAGE MAIN BANNER START */
section.article_page_main_banner_wrapper {
    padding-top: 64px;

    .article_page_main_banner_img_wrapper {
        position: relative;
        border-radius: 20px;
        overflow: hidden;

        .article_page_main_banner_img {
            img {
                display: block;
                width: 100%;
            }
        }

        .article_page_main_banner_title {
            position: absolute;
            left: 32px;
            bottom: 32px;

            h2 {
                margin: 0;
                font-style: normal;
                font-weight: 500;
                font-size: 34px;
                line-height: 1;
                color: #ffffff;
            }
        }

        .go_back {
            position: absolute;
            top: 37px;
            left: 21px;
            display: block;
            text-decoration: none;
            color: #ffffff;
            font-size: 18px;
            font-weight: 500;
            padding: 5px 5px 5px 30px;

            &:before {
                content: '';
                position: absolute;
                top: 50%;
                left: 0;
                display: block;
                width: 22px;
                height: 22px;
                background-image: url("../img/arrow-back.svg");
                background-repeat: no-repeat;
                background-position: center;
                transform: translateY(-50%);
            }
        }
    }
}
/*! SECTION ARTICLE PAGE MAIN BANNER END */

/*! SECTION ARTICLE PAGE MAIN CONTENT START */
section.article_page_main_content_wrapper {
    padding: 32px 0 112px;

    .article_page_main_content {
        font-size: 18px;
        line-height: 1.3;
    }
}
/*! SECTION ARTICLE PAGE MAIN CONTENT END */

/*! SECTION PRIVACY POLICY PAGE CONTENT START */
section.privacy_policy_content_wrapper {
    padding: 64px 0 128px;

    .privacy_policy_content {
        font-size: 18px;

        h2 {
            margin: 0 0 32px;

            span {
                position: relative;
                font-style: normal;
                font-weight: 500;
                font-size: 34px;
                line-height: 1;
                color: #000000;

                &:before {
                    content: '';
                    position: absolute;
                    top: -3px;
                    right: -54px;
                    display: block;
                    width: 43px;
                    height: 43px;
                    background-image: url("../img/lock.svg");
                    background-repeat: no-repeat;
                    background-position: center;
                    background-size: 43px 43px;
                }
            }
        }
    }
}
/*! SECTION PRIVACY POLICY PAGE CONTENT END */

/*! SECTION TERMS OF USE PAGE CONTENT START */
section.terms_of_use_content_wrapper {
    padding: 64px 0 128px;

    .terms_of_use_content {
        font-size: 18px;

        h2 {
            margin: 0 0 32px;

            span {
                position: relative;
                font-style: normal;
                font-weight: 500;
                font-size: 34px;
                line-height: 1;
                color: #000000;

                &:before {
                    content: '';
                    position: absolute;
                    top: -3px;
                    right: -54px;
                    display: block;
                    width: 43px;
                    height: 43px;
                    background-image: url("../img/hand.svg");
                    background-repeat: no-repeat;
                    background-position: center;
                    background-size: 43px 43px;
                }
            }
        }
    }
}
/*! SECTION TERMS OF USE PAGE CONTENT END */

/*! SECTION PERSONAL PAGE START */
section.personal_page_wrapper {
    .personal_page {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: stretch;
        padding: 64px 0 128px;

        .personal_menu_wrapper {
            margin-right: 64px;

            h2 {
                margin: 0 0 32px;
                font-style: normal;
                font-weight: 500;
                font-size: 34px;
                line-height: 1;
                color: #000000;
            }

            .personal_menu {
                a {
                    display: flex;
                    flex-direction: row;
                    justify-content: flex-start;
                    align-items: center;
                    width: 259px;
                    padding: 13.5px;
                    margin-bottom: 10px;
                    text-decoration: none;
                    border-radius: 14px;
                    background-color: transparent;

                    span {
                        font-style: normal;
                        font-weight: 500;
                        font-size: 18px;
                        line-height: 1;
                        color: #000000;
                    }

                    svg {
                        margin-right: 18px;
                        fill: #000000;
                        width: 24px;
                        height: 24px;
                    }

                    &.active {
                        --icon-color: white;

                        background-color: #dc0000;

                        svg {
                            fill: #ffffff;
                        }

                        span {
                            color: #ffffff;
                        }

                    }

                    &.exit {
                        span {
                            color: #dc0000;
                        }
                    }
                }
            }
        }

        .personal_main_content_wrapper {
            .user_profile_form_wrapper {
                padding: 70px 0 0;

                #user-profile-form {
                    width: 670px;

                    .forma {
                        margin-bottom: 32px;

                        .form-group {
                            display: flex;
                            flex-direction: row;
                            justify-content: space-between;
                            align-items: center;
                            margin-bottom: 20px;

                            label {
                                font-style: normal;
                                font-weight: 500;
                                font-size: 18px;
                                line-height: 1;
                                color: #000000;
                            }

                            input {
                                display: block;
                                width: 372px;
                                height: 46px;
                                border: 2px solid rgba(0, 0, 0, 0.25);
                                border-radius: 14px;
                                font-weight: 500;
                                font-size: 18px;
                                line-height: 1;
                                color: rgba(0, 0, 0, 1);
                                text-align: center;
                                outline: none;

                                &::placeholder {
                                    color: rgba(0, 0, 0, 0.25);
                                }
                            }

                            .select_user_position_wrapper {
                                width: 372px;

                                .select2 {
                                    display: block;
