            right: 60px;
            font-style: normal;
            font-weight: 500;
            font-size: 18px;
            line-height: 1;
            color: #ffffff;

            &:before {
                content: '';
                display: block;
                width: 23px;
                height: 23px;
                position: absolute;
                top: 50%;
                left: -2px;
                background-image: url("../img/white_cancel.svg");
                background-repeat: no-repeat;
                background-position: center;
                background-size: 23px 23px;
                transform: translateY(-50%);
            }
        }

        .remodal-content {
            .club_photo_modal_wrapper {
                position: relative;
                width: 922px;
                height: 518px;
                margin: 0 auto;

                img {
                    display: block;
                    width: 922px;
                    height: 518px;
                }

                .slick-arrow {
                    display: block;
                    width: 46px;
                    height: 46px;
                    position: absolute;
                    background: none;
                    border: none;
                    top: 50%;
                    transform: translateY(-50%);

                    img {
                        display: block;
                        width: 46px;
                        height: 46px;
                    }

                    &.slick-next {
                        right: -120px;
                    }

                    &.slick-prev {
                        left: -120px;
                    }
                }

                .slick-dots {
                    position: absolute;
                    bottom: -3px;
                    right: 23px;
                    counter-reset: dot-current dot-total;

                    .presentation {
                        display: block;
                        font-size: 0;
                        width: 0;
                        height: 0;
                        counter-increment: dot-current dot-total;
                        white-space: nowrap;

                        span {
                            display: none;
                        }

                        &.active ~ .presentation {
                            counter-increment: dot-total;
                        }
                    }

                    .presentation:last-child {
                        display: block;
                        position: absolute;
                        right: 40px;
                        width: 90px;
                        height: auto;
                        text-align: center;

                        &:before {
                            content: counter(dot-current);
                            color: white;
                            display: inline-block;
                            font-size: 30px;
                        }

                        &:after {
                            content: '/' counter(dot-total);
                            font-size: 16px;
                            color: white;
                            display: inline-block;
                        }
                    }
                }

                &:not(.slick-initialized) {
                    .slide_item:not(:first-child) {
                        opacity: 0;
                    }
                }
            }
        }
    }
}

